$blue-dark: #1e384c;
$blue: #2c7fbe;
$blue-light: #32bafa;
$green: #02c39a;

$stage-bg: $blue-dark;

.container {
  height: 100%;
  position: relative;
  max-width: 500px;
  margin: auto;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

.waves {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: hue 30s linear infinite;

  transition: 0.4s transform ease;
  transform-origin: bottom center;

  @media (min-width: 500px) {
    border-radius: 5px;
  }
}
.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: wave 1s linear infinite;

  &.waveFront {
    color: $blue-light;
  }

  &.waveBack {
    color: $blue;
    animation-direction: reverse;
  }
}

.water {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: currentColor;
  transition: height 300ms;

  svg {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 99.9%;
  }
}
.water:first-of-type {
  transform: translate(-100%, 0);
}

svg {
  fill: currentColor;
}

@keyframes wave {
  0% {
    transform: translate3d(0, 0, 0);
  }
  50% {
    transform: translate3d(50%, 0.5em, 0);
  }
  100% {
    transform: translate3d(100%, 0, 0);
  }
}
@keyframes hue {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
